
<template>
  <div class="activeView">
    <h3></h3>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>

      <el-form-item label=""  prop="region">
        <el-select class="select-width" v-model="ruleForm.region" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" required>
        <el-col :span="11">
          <el-form-item prop="date1">
            <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-form-item prop="date2">
            <el-time-picker type="fixed-time" placeholder="" v-model="ruleForm.date2" style="width: 100%;"></el-time-picker>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" prop="delivery">
        <el-col :span="2">
          <el-switch class="switch-left" on-text="" off-text="" v-model="ruleForm.delivery"></el-switch>
        </el-col>
      </el-form-item>


      <el-form-item label="" prop="type">
        <el-checkbox-group v-model="ruleForm.type">
          <el-checkbox label="/" name="type"></el-checkbox>
          <el-checkbox label="" name="type"></el-checkbox>
          <el-checkbox label="" name="type"></el-checkbox>
          <el-checkbox label="" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item label="" prop="resource">
        <el-radio-group v-model="ruleForm.resource">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="" prop="desc">
        <el-input type="textarea" v-model="ruleForm.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"  @click="submitForm('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>


</template>

<style>
  .activeView{
    padding: 50px;
    width:80%;
    /*text-align: center;*/
  }
  .select-width{
    width:100%;
  }
</style>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
          ],
          region: [
            { required: true, message: '', trigger: 'change' }
          ],
          date1: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
          date2: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
          type: [
            { type: 'array', required: true, message: '', trigger: 'change' }
          ],
          resource: [
            { required: true, message: '', trigger: 'change' }
          ],
          desc: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log('submit!');
            console.log(this.ruleForm);
            var newActiveData={};
            var a = new Date(this.ruleForm.date1);
            newActiveData.name=this.ruleForm.name;
            newActiveData.region=this.ruleForm.region;
            newActiveData.date1=this.formatDate(a);
            this.$store.state.activeData.push(newActiveData);

            this.$message({
              message: '',
              showClose: true,
              type: 'success'
            });

          } else {
            console.log('error submit!!');
            this.$message({
              message: '',
              showClose: true,
              type: 'warning'
            });
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      formatDate(date) {//
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? '0' + m : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        return y + '-' + m + '-' + d;
      }
    }
  }

</script>



