<template>
  <div>
    <h2></h2>
    <el-tabs type="border-card">
      <el-tab-pane label="">
        <el-table
          :data="tableData5"
          style="width: 100%">
          <el-table-column type="expand">
            <template scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="">
                  <span>{{ props.row.name }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ props.row.shop }}</span>
                </el-form-item>
                <el-form-item label=" ID">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label=" ID">
                  <span>{{ props.row.shopId }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ props.row.category }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ props.row.address }}</span>
                </el-form-item>
                <el-form-item label="">
                  <span>{{ props.row.desc }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column label=" ID" prop="id"></el-table-column>

          <el-table-column label="" prop="name"></el-table-column>

          <el-table-column label="/" prop="price"></el-table-column>

          <el-table-column label="" width="150px">
            <template scope="counts">
              <el-input-number size="small" :min="1" v-model="counts.row.count"></el-input-number>
            </template>
          </el-table-column>

          <el-table-column label="/" prop="total">
            <template scope="totals">
              {{totals.row.price*totals.row.count}}
            </template>

          </el-table-column>

          <el-table-column label="" width="200px">
            <template scope="scope">
              <el-button size="small" @click="orderDetail(scope.$index, scope.row)"></el-button>

              <el-button size="small" type="success" v-if="!scope.row.subOrder" @click="orderSubmit(scope.$index, scope.row)"></el-button>

              <el-button size="small" type="danger" v-if="scope.row.subOrder" @click="orderDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>

        <!---->
        <el-dialog title="" v-model="dialogFormVisible">
            <el-form label-position="left" ref="ordersubmitbox" :model="ordersubmitbox" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ ordersubmitbox.name }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ ordersubmitbox.shop }}</span>
              </el-form-item>
              <el-form-item label=" ID">
                <span>{{ ordersubmitbox.id }}</span>
              </el-form-item>
              <el-form-item label=" ID">
                <span>{{ ordersubmitbox.shopId }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ ordersubmitbox.category }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ ordersubmitbox.address }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ ordersubmitbox.desc }}</span>
              </el-form-item>
              <el-form-item label="/">
                <span>{{ ordersubmitbox.price }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ ordersubmitbox.count }}</span>
              </el-form-item>
              <el-form-item label="/">
                <span>{{ ordersubmitbox.price*ordersubmitbox.count }}</span>
              </el-form-item>
            </el-form>
                <span slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="payment(seleIndex)"></el-button>
                </span>
        </el-dialog>

      </el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
    </el-tabs>
