<style>
  .demo-table-expand {
    font-size: 0;
    text-align: left;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 49%;
  }
</style>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        ordersubmitbox:{
          id: '',
          name: '',
          price: '',
          count: '',
          subOrder: false,
          category: '',
          desc: '',
          address: '',
          shop: '',
          shopId: ''
        },
        seleIndex:'',
        dialogFormVisible: false,
        tableData5: [{
          id: '12987122',
          name: '',
          price: '11',
          count: 4,
          subOrder: false,
          category: '',
          desc: '',
          address: '',
          shop: '',
          shopId: '10333'
        }, {
          id: '12987123',
          name: '',
          price: '12.5',
          count: 3,
          subOrder: false,
          category: '',
          desc: '',
          address: '',
          shop: '',
          shopId: '10333'
        }, {
          id: '12987125',
          name: '',
          price: '9',
          count: 1,
          subOrder: false,
          category: '',
          desc: '',
          address: '',
          shop: '',
          shopId: '10333'
        }, {
          id: '12987126',
          name: '',
          price: '8.5',
          count: 6,
          subOrder: false,
          category: '',
          desc: '',
          address: '',
          shop: '',
          shopId: '10333'
        }]
      }
    },
    methods: {
      orderDetail(index, row) {
        console.log(index, row);
      },
      orderDelete(index, row) {
        console.log(index, row);
        this.tableData5[index].subOrder = false;
      },
      orderSubmit(index, row) {
        console.log(index, row);
        this.seleIndex=index;
        this.dialogFormVisible = true;
        this.ordersubmitbox=this.tableData5[index];
      },
      payment(seleIndex){
        this.tableData5[seleIndex].subOrder = true;
        this.dialogFormVisible = false;
      }
    }
  }
</script>



